import React from 'react';
import RegistrationManager from './RegistrationManager';
import RegistrationManagerUnlocked from './RegistrationManagerUnlocked';
// import './App.css';
import { Provider } from "react-redux";
import { store } from './store'
import { BrowserRouter as Router, Route } from 'react-router-dom';
import RegistrationManagerSet2 from './RegistrationManagerSet2';
import FaceEmotion from './FaceEmotion';

const App = (props) => {
  
    return (
    <Provider store={store}>
      <Router basename={'/static/enumeracy/index.html'}>
      <Route exact path="/" component={RegistrationManager} />
      <Route exact path="/st" component={RegistrationManagerUnlocked}/>
      <Route exact path="/set2" component={RegistrationManagerSet2}/>
      </Router>
       {/* <RegistrationManager></RegistrationManager> */}
       { <FaceEmotion></FaceEmotion> }
     </Provider>

    )
}

export default App;
