import React, { Component } from 'react'
import { connect } from 'react-redux'
import './think.css'
import ReactModal from 'react-modal';
import './modal.css'
import Modal from "react-animated-modal";


let EmotionState;
let TimeTaken;
let imageSrc;
let User;

export class Thinking extends Component {

  constructor(props){
    super(props)
    this.check = true
    this.state = {showModal:false};
  } 

  handleCLoseModal(e){
    this.setState({ showModal: false });
    //this.check=true
  }

  handleModal(){
    if(this.check){
      this.setState({showModal:true})
      this.check=false
    }
  }

  render() {
    if(TimeTaken!==null){
      var str = TimeTaken.split(":")
      var minutes = str[0]
      var seconds = str[1]
      console.log("minuteeeessssseconds",minutes,seconds)
    }
    console.log("IN Thinking",TimeTaken,EmotionState)
    if (Object.keys(EmotionState).length !== 0){
      console.log("emotion",EmotionState)
      var CurrentEmotion =  Object.keys(EmotionState).reduce((a, b) => EmotionState[a] > EmotionState[b] ? a : b);
      var emotionRepeatCheck="";
      if(CurrentEmotion!==emotionRepeatCheck){
        emotionRepeatCheck=CurrentEmotion
      if(CurrentEmotion==="happiness"){
        CurrentEmotion = "Happy"
        this.handleModal()
      }
      if(CurrentEmotion==="sadness"){
        CurrentEmotion = "Sad"
      }
    }
      console.log("current Emotion",CurrentEmotion)

      }
    return (
      <div>
        
       <Modal
                    visible={this.state.showModal}
                    closemodal={() => this.setState({ showModal: false })}
                    type="flipInX"
                >
      <div style={{display: User!==null?"":"none"}}>
       <img name="img" src={imageSrc} alt="" />
       </div>
       <div style ={{display: Object.keys(EmotionState).length!==0 && User!==null ? "" : "none"}}>
        <h1 name="emotion">{User} is {CurrentEmotion} </h1>
       </div>
       <div style ={{display: TimeTaken!=null ? "" : "none"}}>
       <h1>Last Activity took :</h1><h1 name="time"> {minutes} minutes {seconds} seconds</h1>
       </div>
      </Modal>
      <button className="btn" onClick={() => this.setState({ showModal: true })}>
                    Show Thinking
      </button>

       <div>
        <ReactModal 
           isOpen={this.state.showModal}
           className="modal"
        >
        <p className="pclass">Are you Distressed?</p>
        <button className="actions">Yes</button>
        <button className="actions">No</button>
        <button className="close" onClick={this.handleCLoseModal.bind(this)}>X</button>
        </ReactModal>
        
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  imageSrc = state.currentImageSrc
  TimeTaken = state.timeTaken
  EmotionState = state.emotion
  User = state.user
  return {imageSrc:state.currentImageSrc,TimeTaken:state.timeTaken,EmotionState:state.emotion,User:state.user}
}

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(Thinking)
