import React, { Component } from 'react'
import { connect } from 'react-redux'
import './css/bootstrap.min.css';
import './css/fonts.css';
import './css/style.css';
import {setManagerState, setStartTime} from './rootSlice';

let user;
class ActivityWelcome extends Component {
 
    handleButton() {
        this.props.handleB()
    }
    handleEndButton() {
        this.props.handleEndB()
    }
    render() {
        return (
            <div class="container d-flex h-100">
                <div class="tablet-background">
                <div class="content-inside-tablet chat-screen">
                <nav class="navbar navbar-expand-md d-flex">
                <div class="container">
                <a class="navbar-brand" href="">Enumeracy</a>
                <div><a href="#" onClick={this.handleEndButton.bind(this)} class="button btn-default btn-small">Let's end for today</a></div>
                </div>
                </nav>
                <div class="chat-content container d-flex h-100">
                <div class=" welcome-message row justify-content-center align-self-center">
                <h1 class="photo-place">Hi <span class="current-client ">{user}</span>, are you ready to tackle<br /> some activities?</h1>
                <div class="instructions"><button onClick={this.handleButton.bind(this)} class="button btn-default ">I'm ready</button></div>
                </div>
                </div>
                </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    user = state.user
    return {user:state.user}
}

const mapDispatchToProps = dispatch => {
    return {
    handleB: () => {
        dispatch(setStartTime(performance.now()))
        dispatch(setManagerState("LessonActivity"))
    },
    handleEndB: () => {
        //dispatch(setStartTime(performance.now()))
        dispatch(setManagerState("startscreen"))
    }
}
}

export default connect(mapStateToProps, mapDispatchToProps)(ActivityWelcome)
