const mapStateToProps = ( state ) =>  {
  console.log("mapstate",state)
  index = state.activityManager.index
  disabledNext = state.activityManager.disabledNext
  disabledPrev = state.activityManager.disabledPrev
  startTime = state.starttime
  EndTime = state.endtime
  timetkn = state.timeTaken
  job = state.job
  Age = state.age
  jobcategory = state.jobcategory
  feeling = state.feeling
  interests = state.interests
  user = state.user
  anxietyscore = state.anxietyscore
  stepwritedown = state.stepwritedown
  estimateanswerbefore = state.estimateanswerbefore
  return {index:state.activityManager.index,timetkn:state.timeTaken,job:state.job,user:state.user,startTime:state.starttime,EndTime:state.endtime,Age:state.age,jobcategory:state.jobcategory,feeling:state.feeling,interests:state.interests,anxietyscore:state.anxietyscore,stepwritedown:state.stepwritedown,estimateanswerbefore:state.estimateanswerbefore}
}
const mapDispatchToProps = dispatch => {
    return {
      handleNext: () => {
        console.log("INhandleNext")
        let indexL;
        let disabledNext = false
        let disabledPrev = false
        console.log("length",activities[index.category].length)
        console.log("id",index.id)
    
       
        if(index.id < activities[index.category].length-1){
        console.log("Togglenext1stif")
        indexL = index.id + 1
        console.log(indexL)
        dispatch(setIndex({category:index.category,id:indexL}))
        dispatch(setdisabledPrev(disabledPrev))
        return;
        }
        console.log("length2",activities[index.category].length)
        if(index.id >= activities[index.category].length-1){
    
        console.log("Togglenext2ndif")
        if(activities[index.category][index.id].disabled && activities[index.category][index.id].disabled == true){
          console.log("Togglenext3rdif")
          disabledNext = true;
          dispatch(setdisabledNext(disabledNext))
          dispatch(setdisabledPrev(disabledPrev))
          dispatch(setEndTime(performance.now()))
          var time = EndTime - startTime;
          //console.log("log of time",this.props.start,this.end,time)
          var minutes = Math.floor(time / 60000);
          var seconds = ((time % 60000) / 1000).toFixed(0);
          var totalTime = minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
          console.log("timetaken",totalTime)
          dispatch(setTotalTime(totalTime))
          let profile = {"Name":user,"Age":Age,"Occupation":job,"Job Category":jobcategory,"Feeling":feeling,"Interests":interests,"Anxiety Score":anxietyscore,"Do you need to write down every step when answering a problem?":stepwritedown,"Do you try to estimate the answer before and after you work out the answer to a question?":estimateanswerbefore,"Total time to complete Activity":totalTime}
          console.log("inference",profile);
          fetch('http://34.89.118.144:5002/student_data_entry', {
            method: 'POST',
            body: JSON.stringify(profile),
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
        })
        .then(res => res.json())
        .then(data => console.log(data));
        dispatch(setManagerState("endscreen"))
        return;
        }
        dispatch(setIndex({category:"wholenumbers",id:0}))
        dispatch(setdisabledPrev(disabledPrev))
        return;
      }
      
    },
    handlePrev: () => {
      console.log("INhandlePrev")
        let indexL = index.id - 1;
        let disabledPrev = false
        let disabledNext = false
        if(index.id<=0 && index.category=="naturalnumbers"){
          disabledPrev = true
          dispatch(setdisabledPrev(disabledPrev))
          dispatch(setdisabledNext(disabledNext))
          return;
        }
        if(index.id>0){
            dispatch(setIndex({category:index.category,id:indexL}))
            dispatch(setdisabledNext(disabledNext))
            return;
        }
        if(index.id==0 && index.category=="wholenumbers"){
          dispatch(setIndex({category:"naturalnumbers",id:2}))
          dispatch(setdisabledNext(disabledNext))
          return
        }
    } ,
    handleTimeTaken: (timeTaken) => {
      dispatch(setTimeTaken(timeTaken))
    }
} 
}

