import React, { Component } from 'react'
import { connect } from 'react-redux'
import './css/bootstrap.min.css';
import './css/fonts.css';
import './css/style.css'
import {setManagerState,setUser} from './rootSlice';
import img from './images/uncle-steve-porfile.png'

let name1;

export class StartScreen extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
          inputValue: ''
        };
        name1 = this.getQueryVariable('name')
        console.log("The name",name1)
    }

    getQueryVariable(variable)
    {
        var query = window.location.search.substring(1);
        console.log(query)
        var vars = query.split("&");
        console.log(vars) 
        for (var i=0;i<vars.length;i++) {
                    var pair = vars[i].split("=");
                    console.log(pair)
        if(pair[0] == variable){return pair[1];}
         }
         return(false);
    }

    updateInputValue(evt) {
        this.setState({
          inputValue: evt.target.value
        });
      }
    handleButton() {
        this.props.handleB(name1)
    }
    render() {
        return (
            <div className="container d-flex h-100">
                <div className="tablet-background">
                <div className="content-inside-tablet chat-screen">
                <nav className="navbar navbar-expand-md d-flex">
                <div className="container">
                <a className="navbar-brand" href="">Enumeracy</a>
                </div>
                </nav>
                <div className="chat-content container d-flex h-100">
                <div className=" welcome-message row justify-content-center align-self-center">
                <div className="headshot-profile"><img src={img} alt="Steve " /></div>
                <h1>Hi <span className="current-client ">{name1}</span>, My name is Uncle Steve.<br /> Welcome to Enumeracy !</h1>
                <h1><span className="current-client ">We would love to know you!</span></h1>

                {/* <h1><span class="current-client ">May we have your name?</span></h1>
                <div><input type="text" className="input-field" value={this.state.inputValue} placeholder="Name" onChange={evt => this.updateInputValue(evt)} /></div> */}
                <div><button onClick={this.handleButton.bind(this)} className="button btn-default ">Let's get to know each other</button></div>
                </div>
                </div>
                </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    
})

const mapDispatchToProps = dispatch => {
    return {
    handleB: (name) => {
        dispatch(setManagerState("chatbot"))
        dispatch(setUser(name))
    }
}
}

export default connect(mapStateToProps, mapDispatchToProps)(StartScreen)
