
import React, { Component } from 'react';
import './step.css';
import './modal.css';
import './css/bootstrap.min.css';
import './css/fonts.css';
import './css/style.css';

import img1 from './images/steve.png'
import img2 from './images/good-effort-graphic@2x.png';
import img3 from './images/well-done-graphic@2x.png';
import { connect } from 'react-redux';

var t;
let user;
var effort;
var flag;
const mapStateToProps = ( state ) =>  {
    user = state.user;
    return {user:state.user}
}

class AdditionNPActivity extends Component {

  constructor(props) {
    super(props);
    this.state = {check:false,showModal:false,showEffort:false};
    console.log(this.props.otherProps);
    effort = this.props.effort
  }

  
  handleChange = (e) => {
    if(this.state.showEffort===true){
      this.setState({showEffort:false})
    }
      var anschk;
      this.props.otherProps.check=true
      this.end = performance.now()
      var time = this.end - this.props.start;
      console.log("log of time",this.props.start,this.end,time)
      var minutes = Math.floor(time / 60000);
      var seconds = ((time % 60000) / 1000).toFixed(0);
      var timeTaken = minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
      console.log("timetaken",timeTaken)
      const value = e.currentTarget.value;
      console.log(value);
      const activityName = "Addition"
      var activityInference = "";
      if(value==this.props.otherProps.answer){
        anschk="right";
        flag=0
        activityInference = "addition with negative and positive integers known";
        effort = true;
        this.setState({showEffort:true})
      }
      else{
        anschk="wrong";
        flag=1
        activityInference = "difficulty in addition of negative and positive integers"
        effort = true;
        this.setState({showEffort:true})
      }
      this.props.activityStateChange(activityName,value,activityInference,timeTaken,anschk);
    }
  
  handlePopup(){
    console.log("INHANDLEPOPUP")
    if(this.props.otherProps.check===false)
    {
     this.setState({check:true,showModal:true});
    }
  }
  
  handleCLoseModal(e){
    this.setState({ showModal: false });
  }

  render() {
    // var str = this.props.otherProps.question.split(",");
    // a = str[0];
    // b = str[1];
    t=t+60000;
    console.log("TextACRender")
    console.log("Modal",this.state.showModal)
    return (
      <div >
        <div style ={{display: effort===false ? "" : "none"}}>
      <h1>Choose the correct option to fill the blank</h1>
      <div class="activity-quiz">
      <div class="digits">{this.props.otherProps.question}</div>
      <div class="digits nopad">=</div>
      <div class="fill short"></div>
      </div>
      
      <div class="answer-options">
      <ul>
                            <li>
                                <button value="1" onClick={this.handleChange} class="option-list"><span>1</span>
                                </button>
                            </li>
                            <li>
                                <button value="-2" onClick={this.handleChange} class="option-list"><span>-2</span>
                                </button>
                            </li>
                            <li>
                                <button value="9" onClick={this.handleChange} class="option-list"><span>9</span></button>
                            </li>
                            <li>
                                <button value="-9" onClick={this.handleChange} class="option-list"><span>-9</span></button>
                            </li>
                        </ul>
                        </div>
                    </div>
                    <div style ={{display: effort===true && flag===0 ? "" : "none"}}>
            <div class="answer-options result">
                        <ul>
                            <li>
                                <div class={this.props.otherProps.class}><span>{this.props.otherProps.answer}</span>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div class="result-details">
                        <h2>Good effort Well done!</h2>
                        <img src={img3} alt="Good effort !" />
                    </div>
                  
        </div>
        <div style ={{display: effort===true && flag===1  ? "" : "none"}}>
            {/* <div class="answer-options result">
                        <ul>
                            <li>
                                <div class={this.props.otherProps.class}>{this.props.otherProps.answerwords}<span>{this.props.otherProps.answer}</span>
                                </div>
                            </li>
                        </ul>
                    </div> */}
                    <div class="result-details">
                        <h2>Good effort Keep Going!</h2>
                        <img src={img2} alt="Good effort !" />
                    </div>
                    <div style={{display:"none"}}>{flag=0}</div>
        </div>
        {effort = this.props.effort}
        <div style={{display:"none"}}>
        {setTimeout(() => {
          this.handlePopup()
        }, 60000)}
        </div>
        <div style ={{display: this.state.showModal===true ? "" : "none"}}>
        
        <div class="pop-up-overlay"></div>
                <div class="quiz-leave-pop-wrap">
                    <div class="headshot-profile"><img src={img1} alt="Steve" /></div>
                    <h2>Hey <span class="current-user">{user}</span>,<br />I noticed that you did not respond for quite some time. Is everything Ok? Remember, you can click on Let's end for today and continue on another day. You can also skip this part of the process and go straight to lessons. Do you wish to do so? </h2>
                    <div class="pop-up-btn-wrap">
                        <a href="#" id="quitNo" class="btn btn-primary bdr" title="No, I want to continue" onClick={this.handleCLoseModal.bind(this)}>
                            No, I want to continue</a>
                        <a href="#" id="quitYes" class="btn btn-primary" title="No, I want to continue" onClick={this.handleCLoseModal.bind(this)}>
                            Yes, I want to sign off</a>
                    </div>

                </div>
                </div>
        </div>
    )
  }
}
export default connect(mapStateToProps)(AdditionNPActivity);