import { createSlice } from '@reduxjs/toolkit'

const rootSlice = createSlice({
  name: "root",
  initialState: {
    managerState: "startscreen",
    activityManager:{
    activity:"activity1",
    index: {category:"naturalnumbers",id:0},
    disabledNext: false,
    disabledPrev: false,
    },
    timeTaken: null,
    emotion:{},
    currentImageSrc:null,
    user:null,
    age:null,
    job:null,
    feeling:null,
    interests:null,
    jobcategory:null,
    starttime:null,
    endtime:null,
    totaltime:null,
    anxietyscore:null,
    stepwritedown:null,
    estimateanswerbefore:null,
    reasonforjoining:null,
  },
  reducers: {
    setManagerState: (state, action) => { state.managerState = action.payload },
    setActivity: (state, action) => { state.activityManager.activity = action.payload },
    setIndex: (state, action) => { state.activityManager.index = action.payload },
    setdisabledNext: (state, action) => { state.activityManager.disabledNext = action.payload },
    setdisabledPrev: (state, action) => { state.activityManager.disabledPrev = action.payload },
    setEmotion: (state,action) => {state.emotion = action.payload},
    setTimeTaken: (state,action) => {state.timeTaken = action.payload},
    setCurrentImageSrc: (state,action) => {state.currentImageSrc = action.payload},
    setUser: (state,action) => {state.user = action.payload},
    setAge: (state,action) => {state.age = action.payload},
    setJob: (state,action) => {state.job = action.payload},
    setFeeling: (state,action) => {state.feeling = action.payload},
    setInterests: (state,action) => {state.interests = action.payload},
    setJobCategory:(state,action) => {state.jobcategory = action.payload},
    setStartTime:(state,action) => {state.starttime = action.payload},
    setEndTime:(state,action) => {state.endtime = action.payload},
    setTotalTime:(state,action) => {state.totaltime = action.payload},
    setAnxietyScore: (state,action) => {state.anxietyscore = action.payload},
    setStepWriteDown: (state,action) => {state.stepwritedown = action.payload},
    setEstimateAnswerBefore: (state,action) => {state.estimateanswerbefore = action.payload},
    setReasonForJoining : (state,action) => {state.reasonforjoining = action.payload}
  }
})

export const reducer = rootSlice.reducer;

export const {setManagerState,setActivity,setIndex,setdisabledNext,setdisabledPrev,setEmotion,setTimeTaken,setCurrentImageSrc,setUser,setAge,setJob,setFeeling,setInterests,setJobCategory,setEndTime,setStartTime,setTotalTime,setAnxietyScore,setEstimateAnswerBefore,setStepWriteDown,setReasonForJoining } = rootSlice.actions