import React, { useEffect, useRef } from "react";

import { useDispatch} from 'react-redux';
import { setEmotion,setCurrentImageSrc } from './rootSlice';


let data='';
  
  const WebcamCapture = () => {

    const dispatch = useDispatch()
    console.log("I am here")
    const videoRef = useRef(null);
    const photoRef = useRef(null);
    const stripRef = useRef(null);

    useEffect(() => {
      
      getVideo();
    }, [videoRef]);

    const getVideo = () => {
      navigator.mediaDevices
        .getUserMedia({ video: { width: 300 } })
        .then(stream => {
          let video = videoRef.current;
          video.srcObject = stream;
          video.play();
        })
        .catch(err => {
          console.error("error:", err);
        });
    };

    const paintToCanvas = () => {
      let video = videoRef.current;
      let photo = photoRef.current;
      let ctx = photo.getContext("2d");
  
      const width = 320;
      const height = 240;
      photo.width = width;
      photo.height = height;
  
      return setInterval(() => {
        ctx.drawImage(video, 0, 0, width, height);
      }, 200);
    };

    const takePhoto = () => {
      let photo = photoRef.current;
      let strip = stripRef.current;
      try{
      data = photo.toDataURL("image/jpeg");
      }
      catch(error){
        console.log(error);
      }
      //console.log(data);
      const link = document.createElement("a");
      link.href = data;
      link.setAttribute("download", "myWebcam");
      link.innerHTML = `<img src='${data}' alt='thumbnail'/>`;
      strip.insertBefore(link, strip.firstChild);
      dispatch(setCurrentImageSrc(data))
    };



    async function sendImageDatatoServer() {
        let a = Date.now();
        let imageUrl = {"imageSrc":data,"time":a}
        console.log(imageUrl)
        
      await  fetch('http://34.89.118.144:3001/face_service', {
            method: 'POST',
            body: JSON.stringify(imageUrl),
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
        })
        .then(res => res.json())
        .then(data => {try {
          (data[0]["faceAttributes"].emotion?dispatch(setEmotion(data[0]["faceAttributes"].emotion)):console.log(data))
        } catch (error) {
          console.log(error)
        }})
      
        
    }
    
  
    return (
        
    //   <div style ={{display:"none"}}>
          <div style ={{display:"none"}}>
       <video onCanPlay={() => paintToCanvas()} ref={videoRef} style={{display:"none"}}/>
      <canvas ref={photoRef} style={{display:"none"}}/>
      <div>
        <div ref={stripRef} style={{display:"none"}}/>
      </div>
        {setInterval(() => {
            takePhoto()
            sendImageDatatoServer()
            
        }, 90000)}
      </div>
    );
  };

  export default WebcamCapture;