import React, {Component } from 'react';
import ChatbotManager from './ChatbotManager';
// import './App.css';
import { connect } from 'react-redux';
//import FaceEmotion from './FaceEmotion';
import WelcomeScreen from './WelcomeScreen';
import ActivityWelcome from './ActivityWelcome';
import StartScreen from './StartScreen';
import EndScreen  from './EndScreen';
import LessonActivityManagerSet2 from './LessonActivityManagerSet2';

let StateHandler ;
const mapStateToProps = ( state ) =>  {
  console.log("mapstate",state.managerState)
  StateHandler = state.managerState
  return {managerState:state.managerState}
} 



class RegistrationManagerSet2 extends Component {

    constructor(props) {
        super(props);
        this.state = {
            setShowSummary: false
        };
      }


 
render(){  
    console.log("Rerenders",StateHandler);
    const renderComponent = () => {
      if(StateHandler==="startscreen"){
        return <StartScreen></StartScreen>
      }
      if(StateHandler==="welcomescreen"){
        return <WelcomeScreen></WelcomeScreen>
      }
      if (StateHandler==="chatbot") {
        return <ChatbotManager></ChatbotManager>
      }if(StateHandler==="LessonActivity") {
        return <LessonActivityManagerSet2></LessonActivityManagerSet2>;
      }
      if(StateHandler==="activitywelcome"){
        return <ActivityWelcome></ActivityWelcome>
      }
      if(StateHandler==="endscreen"){
        return <EndScreen></EndScreen>
      }
    }
    return (   
      <div>
        {renderComponent()}
        {/* {<FaceEmotion></FaceEmotion> } */}
      </div> 
    )
}
}
export default connect(mapStateToProps)(RegistrationManagerSet2);