import React, { Component } from 'react'
import { connect } from 'react-redux'
import './summary.css'
import Modal from "react-animated-modal";



let User,Age,Job,Feeling,Interest,Emotion,TimeTaken,TotalTime;

export class Summary extends Component {
  constructor(props){
    super(props)
    this.check = true
    this.state = {showModal:true};
  }
  
    render() {
        if(TimeTaken!==null){
            var str = TimeTaken.split(":")
            var minutes = str[0]
            var seconds = str[1]
            console.log("minuteeeessssseconds",minutes,seconds)
          }
          if(TotalTime!==null){
            var str1 = TotalTime.split(":")
            var minutes1 = str1[0]
            var seconds1 = str1[1]
            console.log("minuteeeessssseconds",minutes1,seconds1)
          }
          console.log("IN Summary",TimeTaken,Emotion)
          if (Object.keys(Emotion).length !== 0){
            console.log("emotion",Emotion)
            var CurrentEmotion =  Object.keys(Emotion).reduce((a, b) => Emotion[a] > Emotion[b] ? a : b);
            var emotionRepeatCheck="";
            if(CurrentEmotion!==emotionRepeatCheck){
              emotionRepeatCheck=CurrentEmotion
            if(CurrentEmotion==="happiness"){
              CurrentEmotion = "Happy"
              //this.setState({showModal:true})
            }
            if(CurrentEmotion==="sadness"){
              CurrentEmotion = "Sad"
            }
          }
            console.log("current Emotion",CurrentEmotion)
      
            }
        return (
            <div>
            
            <Modal
                    visible={this.state.showModal}
                    closemodal={() => this.setState({ showModal: false })}
                    type="flipInX"
                >
                <h1 name="head">Summary</h1>
                 <hr></hr>
                <div style ={{display: Age!=null ? "" : "none"}}>
                <h1>User Profile:-</h1>
                <h1>User's Name : {User}</h1>
                <h1>User's Age : {Age}</h1>
                <h1>User's Occupation : {Job}</h1>
                <h1>User's today's feeling : {Feeling}</h1>
                <h1>User's Interests : {Interest}</h1>
            </div>
            <div style ={{display: Object.keys(Emotion).length!==0 && User!==null ? "" : "none"}}>
        <h1>User's Emotion is {CurrentEmotion} </h1>
       </div>
       <div style ={{display: TimeTaken!=null ? "" : "none"}}>
       <h1>User's time for Last Activity took :</h1><h1 name="time"> {minutes} minutes {seconds} seconds</h1>
       </div>
       <div style ={{display: TotalTime!=null ? "" : "none"}}>
       <h1>User's time for Total Activity took :</h1><h1 name="time"> {minutes1} minutes {seconds1} seconds</h1>
       </div> 
                </Modal>

                <button className="btn" onClick={() => this.setState({ showModal: true })}>
                    Show Summary
      </button>

        </div>
        )
    }
}

const mapStateToProps = (state) => {
    TimeTaken = state.timeTaken
    Emotion = state.emotion
    User = state.user
    Age = state.age
    Feeling = state.feeling
    Interest = state.interests
    Job = state.job
    TotalTime = state.totaltime
    return {TimeTaken:state.timeTaken,Emotion:state.emotion,User:state.user,Age:state.age,Feeling: state.feeling,Interest:state.interests,Job:state.job,totaltime:state.totaltime}
}

const mapDispatchToProps = {
    
}

export default connect(mapStateToProps, mapDispatchToProps)(Summary)
