import React, { Component } from 'react'
import img1 from './images/music-icon.png'
import img2 from './images/photography-icon.png'
import img3 from './images/cooking-icon.png'
import img4 from './images/nature-icon.png'
import img5 from './images/footbal-icon.png'
import img6 from './images/gaming-icon.png'
import img7 from './images/knitting-icon.png'
import './css/style1.css';
import i1 from './images/uncle-steve-porfile.png';
var resultvalue = '';
export default class OptionActivity1 extends Component {
    constructor(props) {
        super(props);
        console.log("ddsddd",props)
        this.flag=0
        this.state = {
          trigger: false,
          active:false,
          active1:false,
          active2:false,
          active3:false,
          active4:false,
          active5:false,
          active6:false
        };
    
        //this.triggetNext = this.triggetNext.bind(this);
      }

      triggetNext(a) {
        //var currval =  e.currentTarget.value;
        if(a==="Music"){
            this.setState({ active: true })
        }
        if(a==="Photography"){
            this.setState({ active1: true })
        }
        if(a==="Cooking"){
            this.setState({ active2: true })
        }
        if(a==="Nature"){
            this.setState({ active3: true })
        }
        if(a==="Sports"){
            this.setState({ active4: true })
        }
        if(a==="Gaming"){
            this.setState({ active5: true })
        }
        if(a==="Knitting"){
            this.setState({ active6: true })
        }
        console.log("currrrvallll",a);
        resultvalue = resultvalue + a;
        this.flag = this.flag+1;
        if(this.flag===3){
        this.props.triggerNextStep({value:resultvalue,trigger:'9'});
        }
     }
    render() {
        return (
            <div class="rsc-cs sc-bwzfXH jKLQUD">
            <div class="admin-chat-line chat-head">
            <div class="rsc-ts-image-container sc-htoDjs vmYlS"> <img class="rsc-ts-image sc-gzVnrw cwuCQv" src={i1} alt="avatar"></img></div>
                <div class="rsc-ts-bubble sc-bZQynM heWMyl">
                Which of the following interest area make you happy?(Please select upto 3)</div>
                                <div class="hobby-icons">
                                <ul>
                                    <li className={this.state.active ? 'liactive': ''} value="Music" onClick={this.triggetNext.bind(this,"Music")}><img src={img1} alt="Music"></img><span>Music</span></li>
                                    <li className={this.state.active1 ? 'liactive': ''} value="Photography" onClick={this.triggetNext.bind(this,"Photography")}><img src={img2} alt="Music"></img><span>Photography</span></li>
                                    <li className={this.state.active2 ? 'liactive': ''} value="Cooking" onClick={this.triggetNext.bind(this,"Cooking")}><img src={img3} alt="Music"></img><span>Cooking</span></li>
                                    <li className={this.state.active3 ? 'liactive': ''} value="Nature" onClick={this.triggetNext.bind(this,"Nature")}><img src={img4} alt="Music"></img><span>Nature</span></li>
                                    <li className={this.state.active4 ? 'liactive': ''} value="Sports" onClick={this.triggetNext.bind(this,"Sports")}><img src={img5} alt="Music"></img><span>Sports</span></li>
                                    <li className={this.state.active5 ? 'liactive': ''} value="Gaming" onClick={this.triggetNext.bind(this,"Gaming")}><img src={img6} alt="Music"></img><span>Gaming</span></li>
                                    <li className={this.state.active6 ? 'liactive': ''} value="Knitting" onClick={this.triggetNext.bind(this,"Knitting")}><img src={img7} alt="Music"></img><span>Knitting</span></li>
                                </ul>
                            </div>
            </div>
            </div>
        )
    }
}
